.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	transform: translateZ(0);
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 5%;
		left: 0;
		width: 82%;
		transform: translate(-15%, 0);
		@include max-md {
			width: 150%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 2%;
		right: 0;
		width: 50%;
		transform: translate(15%, 0);
		@include max-md {
			bottom: 10%;
			width: 125%;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6rem;
			align-items: center;
		}
		@include max-xs {
			transform: scale(0.75);
		}
		.cover-content {
			text-align: center;
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				width: 40%;
				@include max-lg {
					width: 60%;
				}
				@include max-md {
					width: 100%;
				}
				.elements-wrapper-absolute {
					.background-element {
						overflow: hidden;
						border-radius: var(--border-radius);
					}
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 40%;
					height: auto;
					margin: -5rem auto 0;
					@include max-lg {
						width: 80%;
					}
					.frame {
						width: 100%;
						height: auto;
						object-fit: cover;
					}
					.cover-background {
						position: absolute;
						top: 50%;
						left: 50%;
						width: 50%;
						object-fit: cover;
						height: auto;
						aspect-ratio: 6/7;
						transform: translate(-50%, -55%);
						border-radius: 300px 300px 0 0;
					}
				}
				.cover-description {
					position: relative;
					padding: 0 1rem 2rem;
					text-align: center;
					color: var(--color-dark);
					@include max-lg {
						padding: 0 0.5rem 2rem;
					}
					* {
						position: relative;
					}
					.title {
						margin: 1rem 0;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
