.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.rsvp-wrapper {
		position: relative;
		color: var(--color-dark);
		.elements-wrapper-absolute {
			.background-element {
				overflow: hidden;
				border-radius: 300px 300px 12px 12px;
			}
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			aspect-ratio: 7/8;
			.rsvp-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 300px 300px 5px 5px;
			}
		}
		.rsvp-form {
			position: relative;
			padding: 3rem 2rem 0;
			@include max-md {
				padding: 2rem 1.5rem 0;
			}
			.title-section {
				&::before {
					all: unset;
				}
				&-ornament-left {
					path {
						fill: var(--color-dark);
					}
				}
				&-ornament-right {
					path {
						fill: var(--color-dark);
					}
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
			.btn-custom {
				margin-bottom: -1.25rem;
			}
		}
	}
}
