.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 50%;
		width: 250%;
		transform: translate(-50%, 0);
		@include min-md {
			top: 0;
			width: 130%;
			transform: translate(-40%, 0);
		}
		@include min-lg {
			transform: translate(-40%, -20%);
		}
	}
	.orn-2 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 250%;
		transform: translate(-50%, 0) scaleX(-1);
		@include min-md {
			width: 130%;
			transform: translate(-70%, 0) scaleX(-1);
		}
		@include min-lg {
			top: unset;
			bottom: 0;
			transform: translate(-60%, 0) scaleX(-1);
		}
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				color: var(--color-dark);
				padding: 5rem 2rem 2rem;
				@include max-md {
					padding: 3rem 1rem 2rem;
				}
				.elements-wrapper-absolute {
					.background-element {
						overflow: hidden;
						border-radius: var(--border-radius);
					}
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
		margin-bottom: -1.5rem;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
	}
}
