.reminder-wrap {
	position: relative;
	display: block;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.bird {
		position: relative;
		width: 240px;
		margin-right: -4rem;
		z-index: 5;
		pointer-events: none;
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 180px;
			margin-right: -2rem;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		border-radius: var(--border-radius);
		overflow: hidden;
	}
	.orn-2 {
		position: absolute;
		top: 40%;
		left: 50%;
		width: 200%;
		transform: translate(-50%, -50%) scaleX(-1);
		@include min-md {
			width: 120%;
		}
		@include min-lg {
			width: 100%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 15%;
		width: 20px;
		transform: translate(0, -50%);
		path {
			fill: var(--color-button-trans);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 15%;
		width: 20px;
		transform: translate(0, 50%);
		path {
			fill: var(--color-button-trans);
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		display: flex;
		align-items: center;
		max-width: 100%;
		margin: 0 auto;
		@include max-md {
			transform: translateX(-10%);
		}
	}
	.btn-reminder {
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
