.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		overflow: hidden;
		@include max-md {
			padding: 2rem 1rem;
		}
		.orn-1 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			img {
				object-position: top;
			}
		}
	}
}
