.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 85%;
			@include max-lg {
				width: 95%;
			}
			@include max-md {
				width: 100%;
				transform: translate(-55%, 20%);
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 8/9;
		margin-bottom: 3rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: 300px 300px 5px 5px;
			}
		}
	}
}
