.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			display: none;
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.orn-1 {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 120%;
			transform: translate(-50%, -50%);
			@include max-md {
				left: 35%;
				top: 0;
				width: 175%;
			}
		}
	}
	.couple-body {
		position: relative;
		.couple-content {
			position: relative;
			margin-bottom: 21rem;
			@include max-md {
				margin-bottom: 18rem;
			}
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1px;
		color: var(--color-dark);
		&.man {
			.image-wrap {
				margin-right: auto;
				margin-left: -1.5rem;
				.bird {
					right: 0;
					transform: translate(70%, -60%) scaleX(-1);
				}
			}
			.couple-description {
				text-align: left;
			}
			.sosmed-wrap {
				left: 2rem;
			}
		}
		&.woman {
			@include max-md {
				margin-top: 12rem;
			}
			.image-wrap {
				margin-left: auto;
				margin-right: -1.5rem;
				.bird {
					left: 0;
					transform: translate(-70%, -60%);
				}
			}
			.couple-description {
				text-align: right;
			}
			.sosmed-wrap {
				right: 2rem;
			}
		}
		.image-wrap {
			width: 85%;
			position: relative;
			aspect-ratio: 7/8;
			margin-top: -18rem;
			@include max-lg {
				margin-top: -12rem;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					position: absolute;
					top: 50%;
					left: 50%;
					width: 50%;
					height: auto;
					aspect-ratio: 7/8;
					object-fit: cover;
					transform: translate(-50%, -60%);
					border-radius: 300px 300px 0 0;
				}
			}
			.bird {
				position: absolute;
				top: 50%;
				width: 50%;
			}
		}
		.couple-description {
			position: relative;
			padding: 0 2rem 2rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: 0;
			transform: translateY(50%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
		.elements-wrapper-absolute {
			.background-element {
				overflow: hidden;
				border-radius: var(--border-radius);
			}
		}
	}
}
