.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200%;
		transform: translate(-30%);
		@include min-md {
			width: 130%;
			transform: translate(-40%);
		}
		@include min-lg {
			width: 120%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 200%;
		transform: translate(30%, 0) scaleX(-1);
		@include min-md {
			width: 130%;
			transform: translate(50%, -50%) scaleX(-1);
		}
		@include min-lg {
			width: 120%;
			transform: translate(50%, -40%) scaleX(-1);
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 250%;
		transform: translate(-50%, -50%);
		@include min-md {
			width: 90%;
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 25rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 23rem;
			}
			@include max-md {
				margin-bottom: 18rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: absolute;
				top: 0;
				left: 50%;
				background: linear-gradient(13deg, #c6a058 0%, #ffda92 100%);
				width: fit-content;
				transform: translate(-50%, 20%);
				padding: 1rem 1rem 0.5rem;
				z-index: 3;
				mask-image: url(../images/mask.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.card {
				background-color: transparent;
				z-index: 5;
				text-align: center;
				color: var(--color-dark);
				border: none;
				.elements-wrapper-absolute {
					.background-element {
						overflow: hidden;
						border-radius: var(--border-radius);
					}
				}
				.card-body {
					position: relative;
					padding: 0 2rem 2rem;
					@include max-lg {
						padding: 0 1.5rem 1.5rem;
					}
					@include max-md {
						padding: 0 1rem 1rem;
					}
					h4 {
						margin: 0 0 1rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 20rem;
	}
	.event-name {
		position: relative;
		color: var(--color-light);
		width: 100%;
		margin-top: -19rem;
		@include max-lg {
			margin-top: -15rem;
		}
		.hogwart {
			position: relative;
		}
		&-body {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 55%;
			transform: translate(-50%, -60%);
			@include max-lg {
				transform: translate(-50%, -55%);
			}
			.date {
				position: relative;
				width: 100%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				margin: 0.5rem 0;
				h5 {
					font-family: $font-secondary;
					font-weight: 700;
					margin-bottom: 0;
					font-size: 36px;
					min-width: 30%;
					margin: 0.15rem 0;
				}
				hr {
					display: block;
					width: 25%;
					height: 2px;
					margin: 0 auto;
				}
			}
			h4 {
				font-family: $font-secondary;
			}
			p {
				margin-bottom: 0;
			}
		}
	}
	.event-place {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		padding: 1.5rem;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
