.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	min-height: 80vh;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-absolute {
		@include max-md {
			width: 230%;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(0, 50%);
		opacity: 0.5;
		.image-element {
			width: 250%;
			@include min-lg {
				width: 150%;
			}
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(0, 0%);
		opacity: 0.25;
		.image-element {
			width: 200%;
			@include min-lg {
				width: 100%;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 150%;
		transform: translate(-35%, -40%);
		@include min-lg {
			width: 80%;
			transform: translate(-35%, -50%);
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translate(0, -40%);
		@include min-lg {
			width: 60%;
			transform: translate(0, -50%);
		}
		.image-element {
			width: 80%;
			@include min-lg {
				width: 60%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 50%;
		left: 0;
		width: 100%;
		transform: translate(0, 50%);
		@include min-md {
			bottom: 35%;
		}
		@include min-lg {
			bottom: 30%;
		}
		.image-element {
			width: 150%;
			@include min-lg {
				width: 100%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 15%;
		left: 0;
		width: 100%;
		transform: translate(0, 0);
		@include min-lg {
			top: 0;
			transform: translate(0, 0);
		}
		@include min-lg {
			width: 50%;
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
